
.hero {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc( 100vh  -  53.6px );
  text-align: center;
  background: url('https://img.freepik.com/free-photo/3d-portrait-people_23-2150793856.jpg?t=st=1726039141~exp=1726042741~hmac=51e7f5b4f52b3c9456c0a438846b5935ec71a32555726aa06f2a57fc27ee3640&w=1060');
 
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 100%, 0 80%);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
 

 
}




.heading-1 {
  font-size: 48px;

  font-family: Arial, Helvetica, sans-serif;
}
.heading-2 {
  font-size: 24px;
 
  margin: 30px;
}
.button {
  background-color: #1a2b4c;
  color: #ffffff;
  padding: 15px 30px;
  font-size: 18px;
}
.button:hover {
  background-color: #0f1d35;
}
@media screen and (max-width:650px){
  .heading-2{
    font-size: 16px;
    color: white;
  }
  .heading-1{
    font-size: 26px;
    color: white;
  }
  .button {
    background-color: #1a2b4c;
    color: #ffffff;
    padding: 10px 20px;
    font-size: 12px;
  }
}
