.education-section {
    text-align: center;
    padding: 50px 20px;
    background: linear-gradient(135deg, #f0f0f0, #e0e0e0);
    color: #333;
}

.education-section h2 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 2px;
    animation: fadeInDown 1s ease-out;
}

.education-tree {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    max-width: 800px;
    position: relative;
}

.education-item {
    margin: 40px 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    animation: fadeInUp 1s ease-out;
}

.icon-container {
    background: #fff;
    border-radius: 50%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2em;
    color: #333;
    margin-right: 20px;
    transition: transform 0.3s ease;
}

.education-item:hover .icon-container {
    transform: scale(1.1);
}

.education-details {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: left;
    position: relative;
    z-index: 1;
    flex-grow: 1;
}

.education-details h3 {
    font-size: 1.5em;
    color: #333;
    margin: 0 0 10px;
}

.education-details p {
    margin: 5px 0;
    color: #555;
}

.tree-connector {
    width: 2px;
    height: 60px;
    background: #ccc;
    position: absolute;
    left: 40px;
    top: 60px;
    transform: translateX(-50%);
    z-index: 0;
}

.tree-connector:before {
    content: '';
    width: 10px;
    height: 10px;
    background: #ccc;
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    z-index: 1;
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
