/* Global Styles */
body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background: #f4f4f9; /* Light background for overall page */
}

/* Projects Section Styles */
.projects {
  padding: 50px 20px;
  background: #f4f4f9; /* Light background */
  min-height: 100vh;
  box-sizing: border-box;
  text-align: center;
}

.title {
  font-size: 2.5em;
  margin-bottom: 40px;
  color: #003d4c; /* Highlight color */
  animation: fadeIn 2s ease-in-out;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  animation: fadeInUp 2s ease-in-out;
}

.project-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.project-card:hover {
  transform: translateY(-10px);
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 0.3s;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

.project-info {
  padding: 20px;
}

.project-title {
  font-size: 1.5em;
  margin: 0 0 10px;
  color: #003d4c; /* Highlight color */
}

.project-description {
  font-size: 1em;
  color: #666;
  margin: 0 0 20px;
}

.project-link {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1em;
  color: #fdf6e3;
  background-color: #003d4c; /* Button background color */
  border: none;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}

.project-link:hover {
  background-color: #002b36; /* Slightly darker on hover */
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
