/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Navbar Container */
.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1a2b4c;
  padding: 10px 40px;
  position: relative;
  z-index: 1000; /* Ensure it's on top of content */
}

/* Logo Styling */
.logo {
  font-size: 20px;
  color: #f0f0f0;
  font-weight: bold;
  font-family: cursive;
  
}

/* Links Container */
.links {
  display: flex;
  align-items: center;
  list-style: none;
  transition: top 0.3s ease-in-out; /* Smooth transition for mobile menu */
}

/* Individual Links */
.links li {
  margin-right: 20px;
}

.links li a {
  text-decoration: none;
  color: #cccccc;
  font-size: 18px;
  transition: color 0.3s ease;
}

/* Hover Effect */
.links li a:hover {
  color: #ffffff;
}

/* Hamburger Icon (Hidden on large screens) */
.fa-bars {
  display: none;
  font-size: 24px;
  color: #ffffff;
  cursor: pointer;
}

/* Mobile Menu */
@media screen and (max-width: 650px) {
  /* Stack links vertically and hide by default */
  .links {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #1a2b4c;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    z-index: 999;
  }

  /* Show links when menu is open */
  .links.set-links {
    top: 0;
  }

  /* Links should take up full width and center align */
  .links li {
    width: 100%;
    text-align: center;
    margin: 0;
  }

  .links li a {
    display: block;
    padding: 15px 0;
    width: 100%;
    font-size: 20px;
    color: #ffffff;
  }

  /* Show the hamburger icon */
  .fa-bars {
    display: block;
  }
}
