/* Home Section Styles */
.home {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px;
    background-color: #f4f4f9; /* Light background */
    min-height: 100vh;
    box-sizing: border-box;
  }
  
  .home-content {
    flex: 1;
    max-width: 50%;
  }
  
  .home-image {
    flex: 1;
    max-width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .home-image img {
    width: 100%;
    height: auto;
    border-radius: 10px; /* Optional: rounded corners */
  }
  
  .intro {
    font-size: 1.2em;
    color: #555;
  }
  
  .name {
    font-size: 3em;
    margin: 0;
    color: #002b36; /* Dark text color */
  }
  
  .tagline {
    font-size: 2em;
    margin: 10px 0;
    color: #003d4c; /* Slightly lighter text color */
  }
  
  .description {
    font-size: 1em;
    color: #666;
    max-width: 600px;
  }
  
  .description a {
    color: #b58900; /* Highlight color */
    text-decoration: none;
  }
  
  .description a:hover {
    text-decoration: underline;
  }
  
  .cta-button {
    display: inline-block;
    padding: 10px 20px;
    margin-top: 20px;
    font-size: 1em;
    color: #fdf6e3;
    background-color: #002b36; /* Button background color */
    border: none;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .cta-button:hover {
    background-color: #003d4c; /* Slightly darker on hover */
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .home {
      flex-direction: column;
      padding: 20px;
    }
  
    .home-content, .home-image {
      max-width: 100%;
    }
  
    .home-image {
      margin-top: 20px;
    }
  }
  