/* Global Styles */
body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background: #f4f4f9; /* Light background for overall page */
}

/* About Section Styles */
.about {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  background: linear-gradient(to right, #003d4c, #002b36); /* Gradient background */
  min-height: 100vh;
  box-sizing: border-box;
}

.about-content1 {
  max-width: 600px;
  text-align: center;
  color: #ecd186;
  animation: fadeIn 2s ease-in-out;
}

.title1 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #ffffff; /* Highlight color */
  animation: slideInFromLeft 1s ease-in-out;
}


.description {
  font-size: 1.2em;
  margin-bottom: 20px;
  line-height: 1.5;
  animation: slideInFromRight 1s ease-in-out;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
