.services-page{
    text-align: center;
    height: calc(100vh - 53.6px);

}
.title::before{
    content: '';
    width: 180px;
    border-radius: 2px;
    height: 3px;
    background-color: black;
    position: absolute;
    bottom: -40px;
}
.pages{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-top: 30px;

}
.pages .page {
    width: 300px;
    min-height: 200px ;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    border: 1px solid green;
}
.pages .page img{
    width: 200px;
}
.page {
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    padding: 16px;
    font-family: 'Roboto', sans-serif;
  }
  
  .service-title {
    font-size: 20px;
    margin-bottom: 8px;
  }
  
  .service-description {
    font-size: 14px;
    color: #555;
  }
  